:root {
  --on-state-color: var(--white-color);
  --not-present-color: var(--dark-gray-color);
  --present-but-not-in-correct-position: var(--yellow-color);
  --present-and-in-correct-position: var(--green-color);
  --white-color: white;
  --gray-color: #e2e8f0;
  --dark-gray-color: #787c7e;
  --green-color: #6aaa64;
  --yellow-color: #cab458;
  --s: .25rem;
  --m: 1rem;
  --l: 2rem;
  --container-width: 800px;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
}

* {
  box-sizing: border-box;
}

.wrapper {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.board {
  max-width: var(--container-width);
  margin: 0 auto;
}

.row {
  gap: var(--s);
  display: flex;
}

.cell {
  border: 2px solid var(--gray-color);
  width: 50px;
  height: 50px;
  margin-bottom: var(--s);
  justify-content: center;
  align-items: center;
  display: flex;
}

.not-present, .present-but-not-in-correct-position, .present-and-in-correct-position {
  color: var(--on-state-color);
}

.cell.not-present {
  background-color: var(--not-present-color);
}

.cell.present-but-not-in-correct-position {
  background-color: var(--present-but-not-in-correct-position);
}

.cell.present-and-in-correct-position {
  background-color: var(--present-and-in-correct-position);
}

.form {
  width: 100%;
  align-items: center;
  gap: var(--s);
  margin-top: var(--m);
  display: flex;
}

.input, button {
  border: 2px solid var(--gray-color);
  border-radius: 4px;
  margin: 0;
}

.input {
  padding: var(--s) 0;
  flex: 1;
}

button {
  padding: var(--s) var(--m);
  background-color: var(--gray-color);
}

